/* Modules */
import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

/* Components */
import { AccountValidationComponent } from '../Components/validations/account-validation/account-validation.component';
import { NetworkValidationComponent } from '../Components/validations/network-validation/network-validation.component';

/* Librairies */

const routes: Routes = [
	{ path: 'account/:token', component: AccountValidationComponent },
	{ path: 'network/:token', component: NetworkValidationComponent }
];

export const VALIDATION_ROUTES: ModuleWithProviders = RouterModule.forChild(routes);



