import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { PipeModule } from '../../Pipes/pipe.module';
import { FooterModule } from '../footer/footer.module';

/* Components */
import { NetworkValidationComponent } from './network-validation/network-validation.component';
import { AccountValidationComponent } from './account-validation/account-validation.component';

/* Services */
import { AccountService } from '../../Services/account.service';
import { NetworksService } from '../../Services/Networks/networks.service';

/* Libraries */

/* Route */
import { VALIDATION_ROUTES } from '../../Routes/validation.routes';

@NgModule({
	imports: [
		VALIDATION_ROUTES,
		CommonModule,
		FormsModule,
		RouterModule,
		PipeModule,
		FooterModule
	],
	declarations: [
		NetworkValidationComponent,
		AccountValidationComponent
	],
	exports: [
	],
	providers: [
		AccountService,
		NetworksService
	]
})
export class ValidationModule { }



