import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params, Data } from '@angular/router';

/* Services */
import { NetworksService } from '../../../Services/Networks/networks.service';

@Component({
	selector: 'app-validation',
	templateUrl: './network-validation.component.html',
	styleUrls: ['./network-validation.component.scss'],
})
export class NetworkValidationComponent implements OnInit {

	count : number = 3
	valid : boolean = false

	constructor(private NetworksService: NetworksService,
				private router: Router,
				private route: ActivatedRoute) { }

	ngOnInit() {
		this.initParams();
	}

	initParams() {
		this.route.params.subscribe(params => {
			console.log(params['token']);
			if (params['token']) {
				this.validationProfileNetwork(params['token']);
			} else
				this.router.navigate(['/']);
		});
	}

	validationProfileNetwork(token) {
		this.NetworksService.validationNetwork(token).subscribe( res => {
			if (res.success) {
				this.valid = true
				this.timer();
			}
		}, err => {
			if (err)
				this.router.navigate(['/']);
		});
	}

	timer() {
		setInterval( () => {
			this.count -= 1
			if (!this.count)
				this.router.navigate(['/']);
		}, 1000);
	}
}



