import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';

/* Service */
import { AccountService } from '../../../Services/account.service';

@Component({
	selector: 'app-account-validation',
	templateUrl: './account-validation.component.html',
	styleUrls: ['./account-validation.component.scss']
})
export class AccountValidationComponent implements OnInit {

	valid : boolean = false
	count : number = 3

	constructor(private AccountService: AccountService,
				private route: ActivatedRoute,
				private router: Router) { }

	ngOnInit() {
		this.initParams();
	}

	initParams() {
		this.route.params.subscribe((params: Params) => {
			this.validateAccount(params['token']);
		});
	}

	validateAccount(token) {
		this.AccountService.accountValidation(token).subscribe( res => {
			this.valid = true
			this.timer();
		}, error => {
			this.router.navigate(['/']);
		});
	}

	timer() {
		setInterval( () => {
			this.count -= 1
			if (!this.count)
				this.router.navigate(['/']);
		}, 1000);
	}
}



